<template>
  <div v-if='showmessage' @click="showmessage=false" style="background-color:powderblue; border-width: 0px;">
        <h4>{{this.message}}</h4>
    </div>
  <div class="submit-form">
    <div v-if="!submitted">
      <div class="form-group">
        <label for="title">Site ID</label>
        <input
          type="number"
          pattern="[\d]{9}"
          class="form-control"
          id="title"
          required
          v-model="tutorial.title"
          name="title"
          minlength="9" maxlength="9" 
        />
      </div>
      <div class="form-group">
        <label for="description">Description</label>
        <input
          class="form-control"
          id="description"
          required
          v-model="tutorial.description"
          name="description"
        />
      </div>
        <div class="form-group">
        <label for="accdate">check date</label>
        <input
          type="date"
          class="form-control"
          id="accdate"
          required
          v-model="tutorial.accdate"
          v-bind:min="now"
          name="accdate"
          
        />
      </div>
        <div class="form-group">
        <label for="acctime">check time</label>
        <select
          class="form-control"
          id="acctime"
          required
          v-model="tutorial.acctime"
          name="acctime"
          onmousedown="if(this.options.length>8){this.size=8;}"  onchange='this.size=0;' onblur="this.size=0;"
        >
      <option value="2000-02-01T09:00:00">09:00</option>
      <option value="2000-02-01T09:30:00">09:30</option>
      <option value="2000-02-01T10:00:00">10:00</option>
      <option value="2000-02-01T10:30:00">10:30</option>
      <option value="2000-02-01T11:00:00">11:00</option>
      <option value="2000-02-01T11:30:00">11:30</option>
      <option value="2000-02-01T12:00:00">12:00</option>
      <option value="2000-02-01T12:30:00">12:30</option>
      <option value="2000-02-01T13:00:00">13:00</option>
      <option value="2000-02-01T13:30:00">13:30</option>
      <option value="2000-02-01T14:00:00">14:00</option>
      <option value="2000-02-01T14:30:00">14:30</option>
      <option value="2000-02-01T15:00:00">15:00</option>
      <option value="2000-02-01T15:30:00">15:30</option>
      <option value="2000-02-01T16:00:00">16:00</option>
      <option value="2000-02-01T16:30:00">16:30</option>
      <option value="2000-02-01T17:00:00">17:00</option>
  </select>
      
      </div>
      <button @click="saveTutorial" class="btn btn-success">Submit</button>
    </div>
    <div v-else>
        <h4>You submitted successfully!</h4>
        <button class="btn btn-success" @click="newTutorial">Add</button>
      </div>
  </div>

</template>
<script>
import TutorialDataService from "../services/TutorialDataService";
export default {
  name: "add-tutorial",

  data() {
    return {
      tutorial: {
        id: null,
        title: "",
        description: "",
        accdate: null,
        acctime: null,
        published: false
      },
      submitted: false,
      message:"",
      showmessage: false,
    };
  },
  methods: {
    saveTutorial() {
      var data = {
        title: this.tutorial.title,
        description: this.tutorial.description,
        accdate: this.tutorial.accdate,
        acctime: this.tutorial.acctime
      };
      TutorialDataService.create(data)
        .then(response => {
          this.tutorial.id = response.data.id;
          console.log(response.data);
          this.submitted = true;
          this.validsubmit = true;
          this.showmessage= false
        })
        .catch(e => {
          console.log(e);
          this.message=e.response.data.message;
          this.showmessage = true;
        });
    },
    
    newTutorial() {
      this.submitted = false;
      this.tutorial = {};
    }
  },
  computed:{
    now() {
      const today = new Date()
      const tomorrow = new Date(today)
      tomorrow.setDate(tomorrow.getDate() + 1)
      var day = ("0" + tomorrow.getDate()).slice(-2);
      var month = ("0" + (tomorrow.getMonth() + 1)).slice(-2);
      var tomorrow_str = tomorrow.getFullYear() + "-" + (month) + "-" + (day);
      return tomorrow_str
  }
  }
};
</script>
<style>
.submit-form {
  max-width: 300px;
  margin: auto;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
</style>